*, p, h1, h2, h3, h4, h6 {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

:root {
  --accent-color: #d41443;
  --main-text-color: #000;
  --secondary-text-color: #907e82;
  --third-text-color: #fff;
  --main-bg-color: #ffb8ca;
  --secondary-bg-color: #fff5f6;
  --third-bg-color: #e17992;
  --bcg-hover-button-red: #c7133e;
  --bcg-hover-button-secondary: #ecebeb;
  --transition-standard: .25s cubic-bezier(.4, 0, .2, 1);
  --main-font-family: "DM Sans", sans-serif;
  --accent-font: "Titan One", sans-serif;
}

header {
  width: 100%;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
}

.page-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  font-family: var(--accent-font);
  color: var(--accent-color);
}

.logo__icon {
  fill: var(--accent-color);
}

.navigation {
  gap: 35px;
  display: flex;
}

.navigation__item {
  color: var(--third-text-color);
}

.navigation__item:hover, .navigation__item:focus {
  color: var(--accent-color);
}

.mobile-container {
  width: 100%;
  height: 100%;
  z-index: 999;
  visibility: hidden;
  background-color: #e17992;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-container__button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 25px;
  right: 20px;
}

.mobile-container__icon {
  fill: var(--accent-color);
}

